body{
    user-select: none;     /* 나중에 클릭안되면 이거 지우기 */

}

.mainBox{
    width: 100%;
}

button {
    background: none;
    border: none;
    cursor: pointer;
  }
  
.ToDoMain {
    background-color: #8AB0AB;
    height: 100vh;
    width: 100vw;
    overflow: auto;
}


.ToDoListTitle{
    margin: 100px auto;
    height: 120px;
    width: 550px;
    background-color: #26413c;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ToDoListTitle div {
    font-family: Inspiration;
    font-size: 80px;
    color: #8AB0AB;
    user-select: none;
}

.ToDoListBox{
    margin: auto;
    height: 500px;
    width: 900px;
    background: linear-gradient(45deg, #3B4D49, #61968F);
    overflow: hidden;

}

.ToDoListBox2{
    margin: 10px auto 0 auto;
    height: 480px;
    width: 880px;
    background-color: #8AB0AB;
    overflow: hidden;

}



.ToDoList_List{
    width: 800px;
    height: 70px;
    margin: 30px auto;
    border-radius: 30px 30px 30px 0;
    background-color: #D9D9D9;
    user-select: none; 
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.ToDoList_List.selected{
    background-color: #61968F;
}



.List{
    margin: 0 50px;
    font-size: 30px;
    font-weight: bold;
    font-family: NanumBrushScript;
;

}

.btn{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 20px;

}

.btn div:nth-child(1){
    margin: 10px -10px 0 0;
}
.btn div:nth-child(2){
    margin: 0 -10px 0 0;
}
.btn div:nth-child(3){
    margin: 3px 0 0 0;
}

.btn div:hover {
    transform: scale(1.1);

}

.Remove.hiden{
    visibility: hidden;
}

.plus_btn_div{
    width: 60px;
    margin: 70px auto;
}

.plus_btn{
    margin: auto;
    transition: transform 0.5s ease-in-out;
}
.plus_btn:hover{
    transform: rotate(90deg) ;
}

.plus_btn:active{
    transform: rotate(0deg) ;
}

.updatebox{
    margin-top: 40px;
    display : flex;
    justify-content: center;
    align-items : center;
}

.updatebox input{
    width: 500px;
    height: 70px;
    border-radius: 30px 0px 0px 30px;
    background-color: #D9D9D9;
    border: none;
    font-size: 30px;
    font-weight: bold;
    font-family: NanumBrushScript;
    padding: 0 20px;
}

.updatebox img {
    margin: 10px;
    
}