body {
  margin: 0px;
  font-family: Dnf,Maple,GrandifloraOne,Inspiration ,Jua,NanumBrushScript,YeonSung,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: Dnf;
  src: url(fonts/DNFForgedBlade-Bold.ttf);
}
@font-face {
  font-family: Maple;
  src: url(fonts/Maplestory\ Bold.ttf);
}
@font-face {
  font-family: GrandifloraOne;
  src: url(fonts/GrandifloraOne-Regular.ttf);
}

@font-face {
  font-family: Inspiration;
  src: url(fonts/Inspiration-Regular.ttf);
}
@font-face {
  font-family: Jua;
  src: url(fonts/Jua-Regular.ttf);
}
@font-face {
  font-family: NanumBrushScript;
  src: url(fonts/NanumBrushScript-Regular.ttf);
}
@font-face {
  font-family: YeonSung;
  src: url(fonts/YeonSung-Regular.ttf);
}